/** text anime */
.js-animate-title .char {
  display: inline-block;
  animation-name: kf-animate-chars;
  animation-duration: 1s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: both;
}

.js-animate-title .char:nth-child(1) {
  -webkit-animation-delay: 0.08s;
  animation-delay: 0.08s;
}

.js-animate-title .char:nth-child(2) {
  -webkit-animation-delay: 0.16s;
  animation-delay: 0.16s;
}

.js-animate-title .char:nth-child(3) {
  -webkit-animation-delay: 0.24s;
  animation-delay: 0.24s;
}

.js-animate-title .char:nth-child(4) {
  -webkit-animation-delay: 0.32s;
  animation-delay: 0.32s;
}

.js-animate-title .char:nth-child(5) {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}

.js-animate-title .char:nth-child(6) {
  -webkit-animation-delay: 0.48s;
  animation-delay: 0.48s;
}

.js-animate-title .char:nth-child(7) {
  -webkit-animation-delay: 0.56s;
  animation-delay: 0.56s;
}

.js-animate-title .char:nth-child(8) {
  -webkit-animation-delay: 0.64s;
  animation-delay: 0.64s;
}

.js-animate-title .char:nth-child(9) {
  -webkit-animation-delay: 0.72s;
  animation-delay: 0.72s;
}

.js-animate-title .char:nth-child(10) {
  -webkit-animation-delay: 0.8s;
  animation-delay: 0.8s;
}

.js-animate-title .char:nth-child(11) {
  -webkit-animation-delay: 0.88s;
  animation-delay: 0.88s;
}

.js-animate-title .char:nth-child(12) {
  -webkit-animation-delay: 0.96s;
  animation-delay: 0.96s;
}

.js-animate-title .char:nth-child(13) {
  -webkit-animation-delay: 1.04s;
  animation-delay: 1.04s;
}

.js-animate-title .char:nth-child(14) {
  -webkit-animation-delay: 1.12s;
  animation-delay: 1.12s;
}

.js-animate-title .char:nth-child(15) {
  -webkit-animation-delay: 1.2s;
  animation-delay: 1.2s;
}

.js-animate-title .char:nth-child(16) {
  -webkit-animation-delay: 1.28s;
  animation-delay: 1.28s;
}

.js-animate-title .char:nth-child(17) {
  -webkit-animation-delay: 1.36s;
  animation-delay: 1.36s;
}

.js-animate-title .char:nth-child(18) {
  -webkit-animation-delay: 1.44s;
  animation-delay: 1.44s;
}

.js-animate-title .char:nth-child(19) {
  -webkit-animation-delay: 1.52s;
  animation-delay: 1.52s;
}

.js-animate-title .char:nth-child(20) {
  -webkit-animation-delay: 1.6s;
  animation-delay: 1.6s;
}

@keyframes kf-animate-chars {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@media screen and (min-width: 768px) {
  .u-space {
    margin-right: 0.3em;
  }
}

.fk-mainContent {
  padding-bottom: 0;
}

.splide__slide::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(240deg, rgba(255, 255, 255, 0.568627451) 10%, rgba(0, 0, 0, 0.1294117647) 90%);
}